*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
}

@-ms-viewport {
  width: device-width;
}

header,
nav,
section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

h2 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

ul {
  margin-top: 0;
  margin-bottom: 1rem;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

img {
  vertical-align: middle;
  border-style: none;
}

a {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}

header {
  background: transparent;
  position: relative;
  width: 100%;
}

.nav-wrapper {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1200px;
  padding: 7px 0;
  position: relative;
  width: 100%;
}

.name a {
  color: #fff;
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  line-height: 1rem;
  text-decoration: none;
  text-shadow: 2px 2px 0px transparent, 1px 1px 0px rgba(255, 255, 255, 0.2), 2px 2px 0px rgba(255, 255, 255, 0.2);
}

.menu li {
  display: inline-block;
  margin-bottom: 0;
  padding: 0 10px;
}

.menu li a {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  line-height: 3rem;
  padding-bottom: 3px;
  position: relative;
  text-decoration: none;
}

.menu li a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fff;
  visibility: hidden;
  transform: scaleX(0);
}

@media only screen and (max-width: 768px) {
  .name {
    padding: 15px 0 10px 15px;
  }

  .menu-trigger {
    background: url(../images/hamburger-icon.svg) no-repeat center/cover;
    display: block;
    height: 35px;
    position: absolute;
    right: 10px;
    top: 15px;
    width: 35px;
    z-index: 10;
  }

  .main-overlay {
    background: rgba(0, 0, 0, 0.72);
    bottom: 0;
    display: none;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: 100vw;
    z-index: 6;
  }

  .main-overlay:after {
    background: url(../images/close-icon.svg) no-repeat center/cover;
    content: "";
    display: block;
    height: 35px;
    position: fixed;
    right: 10px;
    top: 15px;
    width: 35px;
    z-index: 20;
  }

  .menu {
    background: rgba(0, 0, 0, 0.85);
    bottom: 0;
    left: 0;
    max-width: 300px;
    position: fixed;
    top: 0;
    transform: translate3d(-100%, 0, 0);
    width: 100vw;
    z-index: 30;
    padding-bottom: 0;
  }

  nav.nav-wrapper ul li {
    display: block;
    margin-top: 20px;
    padding: 10px 0;
  }

  nav.nav-wrapper ul li a {
    font-size: 1.2rem;
    line-height: 1.4rem;
  }
}

.cover-main {
  background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url("../images/background-hero.jpeg") top center no-repeat/cover;
  height: 60vh;
}

.wrapper {
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
}

#scene {
  padding: 0;
  margin-top: 7.5rem;
}

#scene li {
  list-style-type: none;
}

#scene li.name {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 3.3rem;
  line-height: 3.5rem;
  text-align: center;
  width: 100%;
}

#scene li.title {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  line-height: 2rem;
  margin-top: 4.6rem;
  text-align: center;
  width: 100%;
}

#scene li.btn {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  margin-top: 9rem;
  text-align: center;
  width: 100%;
}

.portfolio-btn,
.contact-btn {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  display: inline-block;
  font-size: 1.1rem;
  margin: 0 auto;
  padding: 10px 20px;
  text-decoration: none;
}

.portfolio-btn {
  margin-right: 2rem;
}

.contact-btn {
  margin-left: 2rem;
}

h2.sub-title {
  display: block;
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  line-height: 2.2rem;
  margin: 70px auto;
  position: relative;
  text-align: center;
  text-shadow: 1px 1px 0px transparent, 1px 1px 0px rgba(0, 0, 0, 0.2), 2px 2px 0px rgba(0, 0, 0, 0.2), 3px 3px 1px rgba(0, 0, 0, 0.2);
}

h2.sub-title:after {
  background-color: #000;
  content: "";
  display: block;
  height: 2px;
  margin: 30px auto 0 auto;
  width: 100px;
}

.masonry-grid {
  position: relative;
}

.grid {
  margin: 0 auto;
}

.grid:after {
  content: "";
  display: block;
  clear: both;
}

.grid-item {
  border: 2px solid rgba(0, 0, 0, 0.5);
  float: left;
  margin-bottom: 1.4rem;
  width: 450px;
}

.wide-img {
  width: 600px;
}

img {
  max-width: 100%;
}

@media only screen and (max-width: 930px) {
  .name a {
    font-size: 1.7rem;
    margin-left: 10px;
  }

  #scene li.name {
    font-size: 2.3rem;
    line-height: 2.5rem;
  }

  #scene li.title {
    font-size: 2rem;
    line-height: 2.2rem;
  }
}

@media only screen and (max-width: 736px) {
  #scene li.name {
    font-size: 2rem;
  }

  #scene li.title {
    font-size: 1.7rem;
  }

  #scene li.btn {
    margin-top: 8.4rem;
  }
}

@media only screen and (max-width: 625px) {
  .grid-item {
    width: initial;
  }
}

@media only screen and (max-width: 560px) {
  .menu {
    max-width: 200px;
  }

  #scene li.name {
    font-size: 1.7rem;
  }

  #scene li.title {
    font-size: 1.4rem;
  }

  #scene li.btn {
    margin-top: 8.4rem;
  }

  .portfolio-btn,
  .contact-btn {
    margin: 0.5rem;
    font-size: 1rem;
    max-width: 200px;
  }

  h2.sub-title {
    font-size: 1.7rem;
    line-height: 1.9rem;
    margin: 30px auto;
  }
}

@media only screen and (max-width: 420px) {
  .cover-main {
    height: 60vh;
  }

  .name a {
    font-size: 1.5rem;
    line-height: 1.2rem;
  }

  #scene li.name {
    font-size: 1.6rem;
  }

  #scene li.title {
    font-size: 1.3rem;
  }

  .portfolio-btn,
  .contact-btn {
    font-size: 1rem;
  }

  h2.sub-title {
    font-size: 1.5rem;
    line-height: 1.7rem;
  }
}
